import Engine from './comm/Engine';
import { SettingStore } from './stores';

export default class LoadMeta {
  data = {networksMeta:[],  imagesMeta:{}};

  initLoad ( callback) {
    const me = this;
    const eng = new Engine();

    eng.get('/metaData/images.json', v=>{
      me.data.imagesMeta = v;
      eng.get('/metaData/networks.json', async v=>{
        me.data.networksMeta=v;
        callback(true);
      });
    });   
  }

  loadStoreData () {
    let d = [];
    try {
      d = JSON.parse(localStorage.getItem('localMyContracts'));
     } catch (e) {}
     SettingStore.dispatch({type:'addMyProject', project:d});
    console.log('==loadStoreData ===>', d);
  }

}
export { LoadMeta };