import React,  { useState, useEffect }  from 'react';
import { Container, Button, Form, Row, Col } from 'react-bootstrap';

import { SettingStore } from '../stores';
import { Engine } from '../comm';

import  { useUNST , useEthereumNetwork,  useSuezHub  } from '../contracts';

import { useUtils, usePostOffice  } from '../customHooks';

import { useParams, useHistory } from 'react-router';
import { Link } from 'react-router-dom';

import { ImageLib } from '../comm';

import  { SuezHub } from './';

export default (props)=> {
  const eng = new Engine();
  const history =  useHistory();

  const {  currentWallet, valueCurrentWallet, isLocalWalletExist, PONetwork } = useEthereumNetwork();
  const {  createProject } = useSuezHub('suezProject',  SettingStore.getState().unst);


  const [ formrec, setFormrec ] = useState({name:''});
  const [ formErr, setFormErr ] = useState( '' );

  const [myProject, setMyProject ] = useState({});
  const [ submitted, setSubmitted ] = useState(false);

  const { hashLink, show, copyableShow } = useUtils();
  const { getPostOffices } = usePostOffice();

  const { poAddress } = useParams();


  const handleTextChange = async (e, id)=> {
    const rec = formrec;
    rec[id] = e.target.value;
    setFormrec(rec);
    
  }

  useEffect(()=>{
    setMyProject(SettingStore.getState().myProject);
    const handleSubscribe = SettingStore.subscribe(() => {
        if (SettingStore.getState()._watcher === 'addedProject') {
          setMyProject(SettingStore.getState().myProject);
        }
    });
    return ()=> {
        handleSubscribe();
    }

  },[]);

// currentWallet

  const resetForm = ()=> {
    setSubmitted(false);
    setFormrec({name:''});
    setFormErr( '');
    SettingStore.dispatch({ type: 'addedPo'});
    history.push('/postOffices');
  }
  const abortMyProject = ()=>{
    SettingStore.dispatch({type:'addMyProject', project:null});
  }

  const btnInit = ()=> <Button className="btn btn-info m-1" onClick={()=>{
    eng.loadingOn();
    // formrec.name,
    createProject( v=>{
      if (v.status === 'success') {
        SettingStore.dispatch({type:'addMyProject', project:{address:v.address, network:currentWallet.network}});
      } else {
        console.log('==PPPPPP====>', v);
      }
      eng.loadingOff();
    });
  }}>Create a project</Button>

  const btnSubmit = ()=> <Button className="btn btn-info m-1" onClick={()=>{

    if (!formrec.name || formrec.name.length < 4 ) {
      setFormErr( 'Name length should not be less than 4 charactors.');
      alert(formrec.name );
      return true;
    } else {
      setFormErr('');
    }
    
    eng.loadingOn();
    // formrec.name,
    createProject( v=>{
      if (v.status === 'success') {
        console.log('vvv===>', v)
        setMyProject(v[0]);
      }
      eng.loadingOff();
    });
  }}>Submit</Button>

  const btnCancel = ()=><Button className="btn btn-secondary m-1" onClick={resetForm}>Cancel</Button>
  
  const projectInit  =  <Container fluid={true} className="p-3 bg-light rounded">
  <Container fluid={true} className="p-3 ">
    <Row>
      <Col xl={3} lg={3} md={4} sm={5} xs={6} className="p-0 pl-2 pr-2 text-left">
          <Container className="d-flex align-items-center justify-content-center p-2 rounded" 
            style={{height:'100%', width:'100%', backgroundColor:'#87CEEB'}}>
            <ImageLib v="project" setting={{className:'', style:{width:'8rem'}}} />
            {/*metaImage.showOrigin('postOffice', {className:'', style:{width:'8rem'}})*/}
          </Container>
      </Col>
      <Col xl={9} lg={9} md={8} sm={7} xs={6} className="alert-secondary rounded p-3">
        <Form.Group className="m-0 p-0">
          <Form.Label className="p-0 m-0 text-info">
              <Container className="text-dark p-0 m-0 pb-2">
                At the first step, to create a project under sidenet. Each project contain 1000 
                placeholder NFT.
                
                {!currentWallet.faucet? '' : <Container fluid={true} className="text-left p-0">If you do not have 
                <b>{!currentWallet || !currentWallet.network || !currentWallet.network.name ? ' test ' : 
                  ` ${currentWallet.network.name} `}</b> ETH, you may apply from 
                    the <a href={currentWallet.faucet} target="_blank">Link of Faucet</a>
                </Container>}
                
                </Container>
            </Form.Label>
        </Form.Group>
        {btnInit()}
      </Col>
  </Row>
  </Container>
</Container>;

  const projectForm  =  <Container fluid={true} className="p-3 bg-light rounded">
    <Container fluid={true} className="p-3 ">
      <Row>
        <Col xl={3} lg={3} md={4} sm={5} xs={6} className="p-0 pl-2 pr-2 text-left">
              <Container className="d-flex align-items-center justify-content-center p-2 rounded alert-success  clearfix" 
                style={{height:'100%', width:'100%'}}>
                <ImageLib v="project" setting={{className:'', style:{width:'8rem'}}} />
              </Container>
        </Col>
        {!submitted ?  <Col xl={9} lg={9} md={8} sm={7} xs={6} className="alert-success rounded p-3">
          <Form.Group className="m-0 p-0">
            <Form.Label className="p-0 m-0 text-dark">
              <Container  fluid={true} className="p-2">
                Your project: {!myProject || !myProject.address ? '' : copyableShow(myProject.address, 5)}
                <span className="ml-2">{!myProject || !myProject.network ? '' : `(Network: ${myProject.network.name})`}</span>
              </Container>
              </Form.Label>
          </Form.Group>

          {getPostOffices().filter(v=>v.address !== poAddress ).
            map((v,k)=> JSON.stringify(v))}

          <Form.Control type="text" defaultValue={formrec.name}
            placeholder="Enter a project name (Limit 32 characters)" className="p-1 m-0 mb-2 border"
            onChange={e=>handleTextChange(e, 'name')}/>

            <Form.Control as="textarea" rows={3} placeholder="Description (Limit 320 characters)" 
            className="p-1 m-0 mb-2 border"  onChange={e=>handleTextChange(e, 'description')} />

            {!currentWallet || !currentWallet.network || PONetwork !== myProject.network.name ? 
                <Container className="text-info p-0 m-0 pb-2">
                  Remind: You need switch your walllet to Ethereum mainnet to submit. This step will cost real Ethereum. 
                </Container> : btnSubmit()}
              <Button className="btn btn-warning ml-2" onClick={abortMyProject}>Abort the Project</Button>
          <span className="m-1 text-danger">{formErr}</span>
        </Col>
         :  <Col xl={9} lg={9} md={8} sm={7} xs={6} className="alert-success rounded p-3">
            Your order has been Submitted! keeping refresh to update, 
            you will see your postoffice on the UNST.
            <Container className="text-center p-3">
             {hashLink('/postOffices/',<Button className="btn btn-info" onClick={resetForm}>OK</Button>)}
            </Container>
            
         </Col>}
    </Row>
    </Container>
  </Container>;

const unAllowedBox = <Container fluid={true} className="p-3 alert-secondary rounded">
      <Row>
        <Col xl={3} lg={3} md={4} sm={5} xs={6} className="p-0 pl-2 pr-2 text-left">
            <Container className="d-flex align-items-center justify-content-center p-2 rounded alert-warning border border-warning" 
              style={{height:'100%', width:'100%'}}>
              <ImageLib v="stop" setting={{className:'', style:{width:'8rem'}}} />
              {/*metaImage.showOrigin('postOffice', {className:'', style:{width:'8rem'}})*/}
            </Container>
        </Col>
        <Col xl={9} lg={9} md={8} sm={7} xs={6} className="p-0 pl-1 pr-3">
          <Container className="alert-warning rounded border border-warning text-left p-3" style={{width:'100%', height:'100%'}}>
            
          Project will be runing on <b>{PONetwork}</b> network. Please connect the page to   
            {!isLocalWalletExist ? '' : ' local wallet or '}  walletConnect and switch wallet network to non {PONetwork} network
          </Container>
          
        </Col>
    </Row>
</Container>

const projectPage =  <Container fluid={true} className="p-0">
     {!currentWallet || !currentWallet.network || !currentWallet.network.name ?    unAllowedBox : 
     !myProject || !myProject.address ? projectInit :  
      <Container fluid={true} className="p-0">
        {projectForm }
        <Container fluid={true} className="p-0 mt-3">
        {getPostOffices().filter(v=>v.address !== poAddress ).
            map((v,k)=> <span key={k}>
              {hashLink('/postOffice/' + v.address, <SuezHub address={v.address}/>)}
          </span>)}
        </Container>
       </Container>}
</Container>


return <Container fluid={true} className="p-3" style={{minHeight:'32rem'}}>
      {projectPage}
  </Container>
}